.item {
    font-style: normal;
    font-weight: 300;
    font-size: 14px;
    border-bottom: 1px solid #E5E5E5;
}
 .item>td:last-child {
    font-weight: 500;

}

.item:last-of-type {
    border-bottom: none;
}

.users {
    border-spacing: 0;
    border-collapse: collapse;
    margin-top: 20px;
}

.buttonsContainer {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
}

.container {
    width: 605px;
    display: flex;
    flex-direction: column;
}

.thead {
    background: #E5E5E5;
    font-weight: 600 ;
    font-size: 14px;
}

.nameCol {
    padding: 16px;
}

